.ui-widget-content {
  color: #6f7e95;
  border-color: #f4f4f4;
}

.ui-widget.ui-widget-content, .ui-timepicker-table.ui-widget-content {
  background: #fff;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.5);
}

.ui-widget {
  font-family: inherit;
  font-size: inherit;
}

.ui-widget-header {
  border: 0;
  background: #59677e;
  color: #fff;
  font-weight: bold;
}
.ui-widget-header a {
  color: #fff;
}

.ui-state-default, .ui-widget-content .ui-state-default, .ui-widget-header .ui-state-default {
  border: 1px solid #ecf2f6;
  background: #fff;
  font-weight: bold;
  color: #6f7e95;
  border-radius: 3px;
}

.ui-widget-header .ui-state-default {
  background: none;
  color: #fff;
  border: 0;
}

.ui-state-hover, .ui-widget-content .ui-state-hover, .ui-widget-header .ui-state-hover, .ui-state-focus, .ui-widget-content .ui-state-focus, .ui-widget-header .ui-state-focus {
  border: 1px solid #639af5;
  background: #639af5;
  font-weight: bold;
  color: #fff;
}

.ui-state-active, .ui-widget-content .ui-state-active, .ui-widget-header .ui-state-active {
  border: 1px solid #47bac1;
  background: #47bac1;
  font-weight: bold;
  color: #fff;
}

.ui-state-highlight, .ui-widget-content .ui-state-highlight, .ui-widget-header .ui-state-highlight {
  border: 1px solid #639af5;
  background: #fff;
  color: #639af5;
}

@media only screen and (max-width: 480px) {
  .ui-dialog {
    left: 10px !important;
    right: 10px !important;
    width: auto !important;
  }
}

.ui-dialog-buttonpane {
  background: #ecf2f6;
  margin: 0.5em -0.2em -0.2em -0.2em;
}
.ui-dialog-buttonpane .ui-button {
  border: 0 !important;
  outline: 0;
}

.ui-widget-overlay {
  background: #000;
  opacity: 0.5;
  filter: Alpha(Opacity=50);
}

.ui-tooltip {
  background: #000 !important;
  color: #fff;
  border: 0;
  box-shadow: none !important;
  opacity: 0.8;
  font-size: 13px;
  pointer-events: none;
}

.ui-datepicker table, .ui-timepicker table {
  margin: 0 0 0.4em;
  background: transparent;
  border-radius: 0;
  box-shadow: none;
}
.ui-datepicker th, .ui-timepicker th {
  background: inherit;
  color: inherit;
  text-transform: inherit;
}
.ui-datepicker tbody tr, .ui-timepicker tbody tr {
  border-bottom: inherit;
}

.ui-datepicker table {
  margin: 0 0 0.4em;
}

.ui-timepicker-table table {
  margin: 0.15em 0 0;
}